.container {
  display: flex;
}
img {
  width: 100px;
}
.center {
  margin: 0 300px 0 280px;
  width: calc(100% - 280px - 300px);
  padding: 20px;
  overflow-x: auto;
  /* height: 100vh; */
}
/* loading */
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
